import React from "react";
import "./contact.css";
import Footer from "../Footer/footer";
import { Link } from "react-router-dom";
function Contact(props) {
  return (
    <div>
      <div class="navbar">
        <h1 class="title1">Xin cho. GanZ c th gip g cho bn?</h1>

        <div class="dropdown">
          <a href="help.html">
            <input
              type="text"
              id="myInput"
              onclick=""
              onkeyup="filterFunction()"
              placeholder="Search for names.."
            />
          </a>
        </div>
      </div>

      <div class="contact-content">
        <h1 class="contact-header">i chm sc khch hng GanZ</h1>
        <p>
          Chng ti mun lng nghe cu hi v  kin ng gp t bn. Hy phn
          hi cho GanZ bit vn  ca bn nh! Chng ti s lin h li bn
          trong 24h tip theo.
        </p>
      </div>

      <div class="contact-fill-info">
        <div class="fill-info">
          <div>
            <p>
              <span class="change-color">*</span> Bn cn h tr vi vai tr l
              ngi bn hay ngi mua?
            </p>
            <select name="user" id="user" onchange="onChangeOption()">
              <option value="seller">Ngi bn</option>
              <option value="customer">Ngi mua</option>
            </select>

            <p>
              <span class="change-color">*</span> Bn thc mc v vn  no?
            </p>
            <select name="topic" id="topic" onchange="onChangeOption()">
              <option value="V vic giao hng ca GanZ">
                V vic giao hng ca GanZ
              </option>
              <option value="Tri nghim mua hng trc tip">
                Tri nghim mua hng trc tip
              </option>
              <option value="i/tr hng vi GanZ">
                i/tr hng vi GanZ
              </option>
              <option value="Cc vn  lin quan n n hng">
                Cc vn  lin quan n n hng
              </option>
            </select>

            <p>
              <span class="change-color">*</span> a ch email ca bn
            </p>
            <input
              class="contact-email-input1"
              id="support-email"
              name="support-email"
              type="email"
              placeholder="in a ch email ca bn"
            />

            <p>
              <span class="change-color">*</span> Ni dung phn hi
            </p>
            <input
              class="contact-input2"
              id="support-content"
              name="support-content"
              type="text"
              placeholder="in thm thng tin chi tit v thc mc/vn  ca bn"
            />

            <p>
              Bn c th nh km tp vi ui l jpg/jpeg/png/pdf/mp4. Dung
              lng tp khng qu 4.5MB.
            </p>

            <input type="file" id="file-uploader" />

            <button type="submit" id="send-request">
              Gi phn hi
            </button>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Contact;
