import { yupResolver } from "@hookform/resolvers/yup";
import axios from "axios";
import React from "react";
import { useForm } from "react-hook-form";
import { useSelector } from "react-redux";
import yup from "../../validate/yupGlobal";
import Footer from "../Footer/footer";
import "./order.css";

Order.propTypes = {};

function Order(props) {
  const stateRedux = useSelector((state) => state.cart);
  const totalPrice = stateRedux.cartAr.reduce(
    (price, item) => price + item.quantity * item.price,
    0
  );
  const phoneRegExp =
    /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
  const schema = yup.object().shape({
    name: yup.string().required("Vui lng nhp tn!"),
    address: yup.string().required("Vui lng nhp a ch!"),
    phone: yup
      .string()
      .min(10, "S in thoi qu ngn, vui lng nhp  10 s!")
      .max(10, "S in thoi qu di, vui lng ch nhp 10 s!")
      .matches(phoneRegExp, "Vui lng nhp s in thoi gm 10 s!"),
    email: yup
      .string()
      .required("Vui lng nhp a ch email!")
      .email("Email invalid"),
  });

  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });
  const onOrderSubmit = (data) => {
    pushData({
      ...data,
      numberOfOrders: stateRedux.cartAr.length,
      paymentMethod: "Thanh ton khi nhn hng",
      order: stateRedux.cartAr,
      total: totalPrice + 20000,
    });
  };
  const pushData = async (data) => {
    await axios.post("http://localhost:3001/orders", data);
  };

  return (
    <div>
      <div className="grid order-container">
        <ul className="breadcrumb">
          <li>
            <a href="./index.html">Trang chnh</a>
          </li>
          <li>
            <a href="./cart.html">Gi hng c nhn</a>
          </li>
          <li>Thanh ton</li>
        </ul>

        <form
          action=""
          id="form-payment"
          onSubmit={handleSubmit(onOrderSubmit)}
        >
          <div className="address-container">
            <div className="address__header">
              <h1 className="address__heading">a ch giao hng</h1>
            </div>
            <div className="address__content">
              <div className="form-group--horizontal">
                <label for="">
                  <span>*</span> H v tn
                </label>
                <div className="form-input">
                  <input
                    id="name"
                    type="text"
                    name="name"
                    className="payment__form-input"
                    placeholder="Nhp tn ngi nhn hng"
                    {...register("name")}
                  />
                </div>
              </div>
              {errors.name && <p className="errors">{errors.name.message}</p>}
              <div className="form-group--horizontal">
                <label for="">
                  <span>*</span> a ch
                </label>
                <div className="form-input">
                  <input
                    id="addAddress"
                    type="text"
                    className="payment__form-input"
                    placeholder="Nhp a ch giao hng"
                    {...register("address")}
                  />
                  <p className="valid__feedback-address"></p>
                </div>
              </div>
              {errors.address && (
                <p className="errors">{errors.address.message}</p>
              )}
              <div className="form-group--horizontal">
                <label for="">
                  <span>*</span> S in thoi
                </label>
                <div className="form-input">
                  <input
                    id="addPhone"
                    type="text"
                    className="payment__form-input"
                    placeholder="Nhp s in thoi ngi nhn hng"
                    {...register("phone")}
                  />
                  <p className="valid__feedback-phone"></p>
                </div>
              </div>
              {errors.phone && <p className="errors">{errors.phone.message}</p>}
              <div className="form-group--horizontal">
                <label for="">Email</label>
                <div className="form-input">
                  <input
                    id="addEmail"
                    type="email"
                    className="payment__form-input"
                    placeholder="Nhp email ngi nhn hng"
                    {...register("email")}
                  />
                  <p className="valid__feedback-email"></p>
                </div>
              </div>
              {errors.email && <p className="errors">{errors.email.message}</p>}
              <div className="address__choose">
                <div className="address"></div>
              </div>
            </div>
          </div>

          <div className="container__body">
            <div className="container__header">
              <ul className="container__header-item header-product">
                <li>Sn phm</li>
              </ul>
              <ul className="container__header-item element-product">
                <li>n gi</li>
                <li>S lng</li>
                <li>Thnh tin</li>
              </ul>
            </div>

            <div className="container__content">
              {stateRedux.cartAr.map((data, index) => (
                <div className="order-product">
                  <div className="product__desc1">
                    <img
                      src={data.image}
                      alt="Product"
                      className="product__img"
                    />
                    <div className="product__text">
                      <label className="form-check-label product__type ms-2">
                        {data.title}
                      </label>
                    </div>
                  </div>
                  <div className="price-quantity-intoMoney">
                    <ul className="content__item containar__product-element">
                      <li>{data.price.toLocaleString("vi-VN") + " " + ""}</li>
                      <li>{data.quantity}</li>
                      <li>
                        {(data.price * data.quantity).toLocaleString("vi-VN") +
                          " " +
                          ""}
                      </li>
                    </ul>
                  </div>
                </div>
              ))}
            </div>

            <div className="container__note">
              <div className="container__message">
                <p className="message__heading">Li nhn:</p>
                <textarea
                  name=""
                  id=""
                  cols="10"
                  rows="5"
                  className="input-message"
                  {...register("message")}
                ></textarea>
              </div>
              <div className="container__delivery">
                <p className="delivery__heading">Ph vn chuyn:</p>
                <span className="delivery__fee">20.000 </span>
              </div>
            </div>
          </div>

          <div className="container__voucher">
            <h4 className="voucher__heading">Voucher</h4>
            <button className="btn voucher__link">
              <span>Chn code </span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>

          {/* <!-- Modal for Voucher --> */}
          {/* <div id="modalVoucher" className="modal-voucher">
         
            <div className="modal-content">
              <div className="modal-display">
                <div className="modal-header">
                  <h2 className="modal-title text-orange">Voucher mua hng</h2>
                  <span className="close">&times;</span>
                </div>
                <div className="modal-body">
                  <div className="body--flex-column">
                    <img
                      src="./asset/img/apology.png"
                      className="voucher-image"
                      alt="Voucher image"
                    />
                    <p>
                      Chng ti rt tic l hin ti cha c voucher no. Mong
                      qu khch thng cm
                      <span>
                        {" "}
                        <i className="fas fa-exclamation"></i>
                      </span>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div> */}
          {/* <!-- End Modal for Voucher --> */}

          <div className="container__payment">
            <div className="payment__header">
              <h4 className="payment__heading">Phng thc thanh ton</h4>
              <div className="container__method">
                <a className="payment-method">Tr tin khi nhn hng</a>
              </div>
            </div>
            <div className="payment__body">
              <p className="money-product">
                Tng tin ca sn phm: <span>{totalPrice} </span>
              </p>
              <p className="money-delivery">
                Ph giao hng: <span>20.000 </span>
              </p>
              <p className="money-total">
                Tng thanh ton:
                <span className="total__money total__money-order">
                  {(totalPrice + 20000).toLocaleString("vi-VN") + " " + ""}
                </span>
              </p>
            </div>
            <div className="payment__footer">
              <p className="payment__footer-note">
                Nhn "Thanh ton" ng ngha vic ng  tt c thng tin.
                <a href="#" className="footer-note-link">
                  GanZ Terms
                </a>
              </p>
              <button type="submit" className="btn-pay2">
                Thanh ton
              </button>
            </div>
          </div>
        </form>
      </div>
      <Footer />
    </div>
  );
}

export default Order;
