import { yupResolver } from "@hookform/resolvers/yup";
import axios from "axios";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import yup from "../../validate/yupGlobal";
import Footer from "../Footer/footer";
import "./register.css";

function Register() {
  const phoneRegExp =
    /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
  const schema = yup.object().shape({
    name: yup.string().required("Vui lng nhp tn!"),
    password: yup.string().required("Vui lng nhp mt khu!"),
    confirmpassword: yup.string().required("Vui lng nhp li mt khu!"),
    phone: yup
      .string()
      .min(10, "S in thoi qu ngn, vui lng nhp  10 s!")
      .max(10, "S in thoi qu di, vui lng ch nhp 10 s!")
      .matches(phoneRegExp, "Vui lng nhp s in thoi gm 10 s!"),
    email: yup
      .string()
      .required("Vui lng nhp a ch email!")
      .email("Email invalid"),
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });
  const onOrderSubmit = (data) => {
    pushData({ ...data, role: "user" });
  };
  const pushData = async (data) => {
    await axios.post("http://localhost:3001/users", data);
    alert("dang ki thanh cong");
  };
  console.log(errors);
  return (
    <div>
      <header className="header">
        <div className="grid">
          <div className="header-with-search">
            <Link to="/" className="header__logo">
              <i className="header__logo-icon fas fa-cannabis"></i>
              <span className="header-search__logo-name">GanZ</span>
            </Link>
          </div>
        </div>
      </header>
      <div className="app__container_register">
        <div className="grid">
          <div className="form-container">
            <form id="form-register" onSubmit={handleSubmit(onOrderSubmit)}>
              <div className="form-login">
                <h1 className="form-header">ng k ti khon</h1>
                <div className="row">
                  <div hidden>
                    <input name="role" id="role" value="user" />
                  </div>
                </div>
                <div className="row">
                  <div className="input-container">
                    <input
                      className="input"
                      type="text"
                      name="name"
                      id="name"
                      placeholder="Nhp tn ca bn..."
                      {...register("name")}
                    />
                    {errors.name && (
                      <p className="errors">{errors.name.message}</p>
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="input-container">
                    <input
                      className="input"
                      type="text"
                      name="phonenumber"
                      id="phonenumber"
                      placeholder="Nhp s in thoi ca bn..."
                      {...register("phone")}
                    />
                    {errors.phone && (
                      <p className="errors">{errors.phone.message}</p>
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="input-container">
                    <input
                      className="input"
                      type="text"
                      name="email"
                      id="email"
                      placeholder="Nhp a ch email ca bn..."
                      {...register("email")}
                    />
                    {errors.email && (
                      <p className="errors">{errors.email.message}</p>
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="input-container">
                    <input
                      className="input"
                      type="password"
                      name="password"
                      id="password"
                      placeholder="Nhp mt khu..."
                      {...register("password")}
                    />
                    {errors.password && (
                      <p className="errors">{errors.password.message}</p>
                    )}
                    <p
                      className="form-message"
                      id="form-message-phonenumber"
                    ></p>
                  </div>
                </div>
                <div className="row">
                  <div className="input-container">
                    <input
                      className="input"
                      type="password"
                      name="confirmpassword"
                      id="confirmpassword"
                      placeholder="Xc nhn mt khu..."
                      {...register("confirmpassword")}
                    />
                    {errors.confirmpassword && (
                      <p className="errors">{errors.confirmpassword.message}</p>
                    )}
                    <p className="form-message"></p>
                  </div>
                </div>
                <div className="row">
                  <div className="btn-register-container">
                    <button type="submit" id="register">
                      TO TI KHON
                    </button>
                  </div>
                </div>
                <div className="row">
                  <div className="or">hoc</div>
                </div>
                <div className="row">
                  <div className="btn-container">
                    <button className="btn facebook-btn">
                      <p className="loginbyfacebook-text">
                        ng nhp bng ti khon Facebook
                      </p>
                      <i className="fab fa-facebook-f"></i>
                    </button>
                  </div>
                </div>
                <div className="row">
                  <h5 className="text-forget-password">
                    Bn  c ti khon !
                    <Link to="/login">
                      <a href="./login" className="text-to-go-login">
                        ng nhp
                      </a>
                    </Link>
                  </h5>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Register;
